var sliderWidth = 96; // 需要设置slider的宽度，用于计算中间位置
var app = getApp();
import common from '../../../publicjs/common.js'
export default {
	data() {
		return {
			tabs: [
				{
					icon: 'icon-zixun',
					title: '精选资讯'
				},
				{
					icon: 'icon-pingtaiguanli',
					title: '平台介绍'
				}
			],
			activeIndex: 0,
			sliderLeft: 0,
			sliderOffset: 0,
			activeArray: [
			  {
				"activeUrl": "222",
				"title": "222",
				"content": "222",
				"actiimgUrimgUrllveUrl": "222",
			  
			   },
			   {
				"activeUrl": "222",
				"title": "222",
				"content": "222",
				"actiimgUrimgUrllveUrl": "222",
			    }
			]
		};
	},
	onLoad:function(){
		//获取字体信息
		common.fonts();
	},
	onShow: function() {
		let this_ = this;
		uni.getSystemInfo({
			success: function(res) {
				this_.sliderLeft = (res.windowWidth / this_.tabs.length - sliderWidth) / 2;
				this_.sliderOffset = (res.windowWidth / this_.tabs.length) * this_.activeIndex;
			}
		});
	},
	computed: {
		NoData() {
			return this.activeArray.length;
		}
	},
	watch: {},
	methods: {
		tabClick: function(e) {
			console.log('可以点击了');
			this.sliderOffset = e.currentTarget.offsetLeft;
			this.activeIndex = e.currentTarget.id;
		}
	}
};
